@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{--primaryRed:#F48A94;--primaryGreen:#3DC99C;--gallery: #efefef;--mercury: #e7e7e7;--abbey: #4f5058;--tundora: #454545;--mine-shaft: #303030}

@-webkit-keyframes dot-keyframes{0%{opacity:0.4;-webkit-transform:scale(1, 1);transform:scale(1, 1)}50%{opacity:1;-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2)}100%{opacity:0.4;-webkit-transform:scale(1, 1);transform:scale(1, 1)}}@keyframes dot-keyframes{0%{opacity:0.4;-webkit-transform:scale(1, 1);transform:scale(1, 1)}50%{opacity:1;-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2)}100%{opacity:0.4;-webkit-transform:scale(1, 1);transform:scale(1, 1)}}.loading-dots{text-align:center;width:100%}.loading-dots--dot{-webkit-animation:dot-keyframes 1.5s infinite ease-in-out;animation:dot-keyframes 1.5s infinite ease-in-out;background-color:#000;border-radius:10px;display:inline-block;height:10px;width:10px;margin-left:5px}.loading-dots--dot:nth-child(2){-webkit-animation-delay:0.5s;animation-delay:0.5s}.loading-dots--dot:nth-child(3){-webkit-animation-delay:1s;animation-delay:1s}

